body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* body{
  height: 100000px;
} */


:where(.css-dev-only-do-not-override-1e5rsp4).ant-input {
  background-color: #EFEFEF;
}

.bg-gradient {
  /* background: linear-gradient(90deg, rgba(121, 79, 153, 1) 22%, rgba(150, 79, 153, 1) 58%, rgba(97, 79, 153, 1) 90%); */
  background: transparent linear-gradient(268deg, #3E3185 0%, #774F9A 26%, #6B2F85 74%, #393185 100%) 0% 0% no-repeat padding-box;
}

.primary-color{
  color: #774F99
}

.font-bold{
  font-weight: 700;
}


.ant-carousel .slick-prev,
.ant-carousel .slick-next,
.ant-carousel .slick-prev:hover,
.ant-carousel .slick-next:hover {
  font-size: inherit;
  color: white;
}

.ant-carousel .slick-prev,
.ant-carousel .slick-prev:hover {
  left: 10px;
  z-index: 2;
  color: rgb(14, 13, 13);
  font-size: 40px;
}

.ant-carousel .slick-next,
.ant-carousel .slick-next:hover {
  right: 40px;
  z-index: 2;
  /* color: white; */
  color: rgb(14, 13, 13);
  font-size: 40px;
  

}

.carasoul-img img{
  width: 100%;
  height: 100%;
  object-fit: cover;
  
}

.footer-wrapper{
  background: #EFEFEF;
  display: flex;
  justify-content: space-between;
  gap: 5rem;
  padding: 2rem;
}

.footer-content-wrapper{
  padding: 0 1rem;
  display: flex;
  flex-direction: column;
  gap: 1.5rem;
  font-weight: 500;
  
}
.footer-content-wrapper h2{
  color: rgb(39, 48, 48);
  font-weight: 700;
  font-size: 1.3rem;
}

.footer-content-wrapper a{
  text-decoration: none;
  font-size: 1.1rem;
  font-weight: 500;
  color: rgb(39, 48, 48);
}
.footer-content-wrapper:has(.socials) {
  width: 180px;
}
.socials{
  display: flex;
  justify-content: space-between;
  
}

.socials a{
  font-size: 1.6rem;
}
.office-detail-wrapper{
  width: 700px;
  border-left:1px solid gray ;
  padding-left: 5rem;
}

:where(.css-dev-only-do-not-override-1e5rsp4).ant-row{
  flex-flow: nowrap;
  overflow-x: scroll;
}
:where(.css-dev-only-do-not-override-1e5rsp4).ant-row::-webkit-scrollbar{
  display: none;
}


